<template>
  <div>
    <div class="d-flex align-stretch main-dash-app">
      <div class="nav-bg" >
        <v-scroll-x-reverse-transition hide-on-leave>
          <DashboardSideBar v-show="nav.drawer" :drw="nav" style="max-height: 100vh;overflow-y: hidden;"></DashboardSideBar>
        </v-scroll-x-reverse-transition>
      </div>
        <v-app class="dashboard-app" style="max-height: 100vh;overflow-y: scroll" >
        <div class="dash-toolbar">
          <v-toolbar
              height="48"
              dense
              class="topheader"
              extended
              flat
          >
            <v-btn
                color="info"
                icon
                @click="nav.drawer=!nav.drawer"
            >

              <v-icon size="28" color="#495057"><template v-if="nav.drawer" >mdi-chevron-right</template><template v-else >mdi-menu</template></v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <p class="ma-0 d-none d-md-block " style="font-size: 22px"></p>
            <v-spacer></v-spacer>
            <TopHeaderBtns/>
          </v-toolbar>
        </div>

        <v-main>
        <v-fade-transition hide-on-leave>
          <div class="mx-3 my-6 mx-md-6 my-md-8">

              <router-view></router-view>

          </div>
        </v-fade-transition>
        </v-main>
        <v-footer class="pa-0 ma-0">
          <DashWebFooter/>
        </v-footer>
        </v-app>
    </div>


  </div>
</template>

<script>
import DashboardSideBar from "@/components/Misc/DashboardSideBar";
import TopHeaderBtns from "@/components/Dashboard/Misc/TopHeaderBtns";
import DashWebFooter from "@/components/Dashboard/Misc/DashWebFooter";

export default {
  name: "DashboardApp",
  components: {DashWebFooter, TopHeaderBtns, DashboardSideBar},
  created() {
    this.drawer = !this.mobileDisplay;
  },
  data() {
    return {
      nav : {
        drawer: true
      }
    }
  },
  computed: {
    mobileDisplay() {
      switch (this.$vuetify.breakpoint.name) {
        case 'lg':
          return false
        case 'xl':
          return false
        default :
          return true
      }
    },
  },

}
</script>

